<template>
  <div id="fullDashDoughunt10" class="bgColor_w">
    <div class="fullDiv">
      <CButton block variant="outline" color="dark" class="fullScreenButton " :class="isFull ? 'fullButtonAll' : 'fullButton'" @click="fullScreenEvent('chartDoughnut10')">
        <CIcon :name="isFull ? 'cilFullscreenExit' : 'cilFullscreen'" class="icon" />
      </CButton>
    </div>
    <CChartDoughnut
      :datasets="defaultDatasets"
      :labels="['Cloud Storage', 'Compute Engine', 'Stackdriver Logging', 'Load Balancer', 'BigQuery', 'Pub/Sub', 'Vision AI', 'Cloud Run', 'Cloud SQL', 'Other']"
      :options="defaultOptions"
    />
  </div>
</template>

<script>
import { CChartDoughnut } from '@coreui/vue-chartjs';

export default {
  name: 'MainChartDoughnut10',
  components: { CChartDoughnut },
  data() {
    return {
      isFull: false,
    };
  },
  computed: {
    defaultDatasets() {
      return [
        {
          backgroundColor: ['	#ff9f4080', '#36a2eb80', '	#ff638480', '#ffcd5680', '	#4bc0c080', '#FF2D2D80', '#FFD30680', '#82D90080', '#FF800080', '	#8F458680', '	#9966ff80'],
          borderColor: ['	#ff9f40', '#36a2eb', '	#ff6384', '#ffcd56', '	#4bc0c0', '#FF2D2D', '#FFD306', '#82D900', '#FF8000', '	#8F4586', '	#9966ff'],
          data: [12, 31, 22, 15, 38, 5, 15, 34, 26, 29, 27],
        },

        // { responsive: true, maintainAspectRatio: false },
      ];
    },
    defaultOptions() {
      return {
        responsive: true,
        maintainAspectRatio: false,
        // 畫布縱橫比
        aspectRatio: 1,
        // 文字右方
        legend: {
          // position: 'right',
          align: 'start',
          labels: {
            fontColor: 'black',
            // fontSize: 16,
            fontFamily: 'Microsoft YaHei',
            padding: 10,
          },
        },
        tooltips: {
          // 加上百分比符號
          callbacks: {
            label: function(tooltipItem, data) {
              var dataLabel = data.labels[tooltipItem.index];
              var html = '';
              for (var dataset in data.datasets) {
                html += data.datasets[dataset].data[tooltipItem.index] + '%';
              }
              dataLabel += ' : ' + html;
              return dataLabel;
            },
          },
        },
      };
    },
  },
  methods: {
    fullScreenEvent(type) {
      this.$emit('type', type);
      this.isFull = !this.isFull;
    },
  },
};
</script>
<style></style>
