<template>
  <div>
    <CCard>
      <CCardBody>
        <CRow>
          <CCol sm="4">
            <CSelect :label="$t('billing.month')" :options="['2021/04', '2021/03', '2021/02', '2021/01']" class="projectSelect" />
          </CCol>
          <CCol sm="2"> </CCol>
        </CRow>
      </CCardBody>
    </CCard>

    <CCardGroup columns class="card-columns cols-2">
      <CCard>
        <CCardHeader>
          <CRow>
            <CCol sm="7">
              <span class="titleFont">{{ $t('dashboard.dailyCost') }}</span>
              <div class="small text-muted  ">{{ getMonth }}</div>
            </CCol>
            <CCol sm="5">
              <CAlert show color="warning">
                <CRow>
                  <CCol class="yellowTitle">{{ getYearMonth }} Total Cost: $0.00</CCol>
                </CRow>
              </CAlert>
            </CCol>
          </CRow>
        </CCardHeader>
        <CCardBody>
          <MainChartLine @type="fullScreenEvent" />
        </CCardBody>
        <CCardFooter style="border:none;" class="pcDisplay">
          <div style="visibility:hidden; "></div>
        </CCardFooter>
      </CCard>

      <CCard>
        <CCardHeader>
          <CRow>
            <CCol sm="7">
              <span class="titleFont ">{{ $t('dashboard.commonServices') }}</span>
              <CRow class="radio">
                <CCol>
                  <input id="1" name="radio" type="radio" value="1" v-model="radioSelected" checked />
                  <label for="1" class="radio-label">{{ $t('dashboard.topFiveservices') }}</label>
                </CCol>
                <CCol>
                  <input id="2" name="radio" type="radio" v-model="radioSelected" value="2" />
                  <label for="2" class="radio-label">{{ $t('dashboard.topTenservices') }}</label>
                </CCol>
              </CRow>
            </CCol>
            <CCol sm="5">
              <CAlert show color="warning">
                <CRow>
                  <CCol class="yellowTitle">
                    {{ pieDate }}
                  </CCol>
                </CRow>
              </CAlert>
            </CCol>
          </CRow>
        </CCardHeader>
        <CCardBody>
          <div style="visibility:hidden" class="mobileDisplay"></div>
          <MainChartDoughnut v-if="doughnut5" @type="fullScreenEvent" />
          <MainChartDoughnut10 v-if="doughnut10" @type="fullScreenEvent" />
        </CCardBody>
        <CCardFooter class="mobileDisplay">
          <div style="visibility:hidden; height:47px;">111</div>
        </CCardFooter>
      </CCard>
    </CCardGroup>
    <CCard>
      <CCardHeader>
        <span class="titleFont ">每月金額(過去六個月)</span>
      </CCardHeader>
      <CCardBody>
        <MainChartBar />
      </CCardBody>
    </CCard>
    <CCard>
      <CCardHeader>
        <span class="titleFont">{{ $t('dashboard.projectsMostUsage') }}</span>
      </CCardHeader>
      <CCardBody>
        <MainChartLineProject labels="months" />
      </CCardBody>
    </CCard>
    <CCard>
      <CCardHeader>
        <span class="titleFont ">{{ $t('dashboard.productMostUsage') }}</span>
      </CCardHeader>
      <CCardBody>
        <MainChartLineProduct border-color="warning" labels="months" />
      </CCardBody>
    </CCard>
  </div>
</template>

<script>
import MainChartLine from './charts/MainChartLine';
import MainChartDoughnut from './charts/MainChartDoughnut';
import MainChartDoughnut10 from './charts/MainChartDoughnut10';
import MainChartBar from './charts/MainChartBar';
import MainChartLineProject from './charts/MainChartLineProject';
import MainChartLineProduct from './charts/MainChartLineProduct';
export default {
  name: 'Dashboard',
  components: {
    MainChartLine,
    MainChartDoughnut10,
    MainChartDoughnut,
    MainChartBar,
    MainChartLineProject,
    MainChartLineProduct,
  },
  // delimiters: ['${', '}'],
  data() {
    return {
      selected: 'Month',
      status: 'not_accepted',
      Total: '12',
      checked: '',
      datas: [],
      radioSelected: 1,
      doughnut5: true,
      doughnut10: false,
      pieDate: '',
      getMonth: '',
      getYearMonth: '',
    };
  },

  mounted() {
    // 日期區間 utils/common.js共用
    this.pieDate = this.common.getDateRange();
    this.getMonth = this.common.getChartMonth();
    this.getYearMonth = this.common.getMonth();
    // console.log(this.getMonth);

    // 圖表全螢幕縮放
    // console.log('run');
    let isFullscreen = document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement || document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen;
    isFullscreen = !!isFullscreen;
    let that = this;
    document.addEventListener('fullscreenchange', () => {
      that.isFull = !that.isFull;
    });
    document.addEventListener('mozfullscreenchange', () => {
      that.isFull = !that.isFull;
    });
    document.addEventListener('webkitfullscreenchange', () => {
      that.isFull = !that.isFull;
    });
    document.addEventListener('msfullscreenchange', () => {
      that.isFull = !that.isFull;
    });
  },
  methods: {
    fullScreenEvent(type) {
      // console.log(type);
      if (type == 'chartLine') {
        // console.log('我是chartLine');
        var el = document.getElementById('fullDashLine');
      } else if (type == 'chartDoughnut') {
        // console.log('我是chartDoughnut');
        var el = document.getElementById('fullDashDoughunt');
      } else if (type == 'chartDoughnut10') {
        var el = document.getElementById('fullDashDoughunt10');
      }

      if (this.isFull) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen();
        }
      } else {
        if (el.requestFullscreen) {
          el.requestFullscreen();
        } else if (el.mozRequestFullScreen) {
          el.mozRequestFullScreen();
        } else if (el.webkitRequestFullScreen) {
          el.webkitRequestFullScreen();
        } else if (el.msRequestFullscreen) {
          el.msRequestFullscreen();
        }
      }
    },
    appear(data) {
      // console.log('data:', data);
    },
  },
  watch: {
    // 傳值到MainChart
    // datas() {
    //   Bus.$emit('datas', this.datas);
    // },
    radioSelected() {
      if (this.radioSelected == '1') {
        this.doughnut5 = true;
        this.doughnut10 = false;
      } else if (this.radioSelected == '2') {
        this.doughnut5 = false;
        this.doughnut10 = true;
      }
    },
  },
};
</script>
