<template>
  <CChartLine :datasets="computedDatasets" :options="computedOptions" :labels="month" />
</template>

<script>
import { CChartLine } from '@coreui/vue-chartjs';
import { getColor, deepObjectsMerge } from '@coreui/utils/src';

export default {
  name: 'MainChartLineProject',
  components: { CChartLine },
  props: {
    ...CChartLine.props,
    borderColor: {
      type: String,
      default: 'rgba(255,99,132,1)',
    },
    backgroundColor: {
      type: String,
      default: 'transparent',
    },
    dataPoints: {
      type: Array,
      default: () => [10, 22, 34, 46, 58, 70, 46, 23, 45, 78, 34, 12, 43],
    },
    label: {
      type: String,
      default: 'Sales',
    },
    pointed: Boolean,
    pointHoverBackgroundColor: String,
  },
  data() {
    return {
      month: '',
    };
  },
  mounted() {
    this.month = this.common.getOneYear();
  },
  computed: {
    pointHoverColor() {
      if (this.pointHoverBackgroundColor) {
        return this.pointHoverBackgroundColor;
      } else if (this.backgroundColor !== 'transparent') {
        return this.backgroundColor;
      }

      return this.borderColor;
    },
    defaultDatasets() {
      return [
        {
          data: this.dataPoints,
          borderColor: getColor(this.borderColor),
          backgroundColor: getColor(this.backgroundColor),
          pointBackgroundColor: getColor(this.pointHoverColor),
          pointHoverBackgroundColor: getColor(this.pointHoverColor),
          label: this.label,
        },
      ];
    },
    pointedOptions() {
      return {
        responsive: true,
        maintainAspectRatio: false,
        aspectRatio: 0,
        scales: {
          xAxes: [
            {
              offset: true,
              gridLines: {
                color: 'transparent',
                zeroLineColor: 'transparent',
              },
              ticks: {
                beginAtZero: true,
                maxTicksLimit: 5,
                fontSize: 2,
                fontColor: 'transparent',
              },
            },
          ],
          yAxes: [
            {
              display: false,
              ticks: {
                display: false,
              },
            },
          ],
        },
        elements: {
          line: {
            borderWidth: 1,
          },
          point: {
            radius: 4,
            hitRadius: 10,
            hoverRadius: 4,
          },
        },
      };
    },
    straightOptions() {
      return {
        responsive: true,
        maintainAspectRatio: false,
        aspectRatio: 0,
        title: {
          display: true,
          text: '專案A',
        },
        legend: {
          display: false,
        },
        scales: {
          xAxes: [
            {
              display: true,
              drawOnChartArea: false,
            },
          ],
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                maxTicksLimit: 5,
                stepSize: 50,
                max: 200,
              },
              gridLines: {
                display: true,
              },
            },
          ],
        },
        elements: {
          line: {
            borderWidth: 2,
            tension: 0, // 禁用贝貝茲曲線
          },
          point: {
            radius: 2,
            hitRadius: 10,
            hoverRadius: 4,
          },
        },
      };
    },
    defaultOptions() {
      const options = this.pointed ? this.pointedOptions : this.straightOptions;
      return Object.assign({}, options, {
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
      });
    },
    computedDatasets() {
      return deepObjectsMerge(this.defaultDatasets, this.datasets || {});
    },
    computedOptions() {
      return deepObjectsMerge(this.straightOptions, this.options || {});
    },
  },
};
</script>
